import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import {Accordion} from 'react-bootstrap';


class Faq extends Component {
    render() {

        const accordionData = [
            {
              title: 'Reserve with Google ?',
              content: 'Reserve with Google"    , ,   Google Search and Maps',
            },
            {
              title: '?',
              content: 'AAppoint   Reserve with Google   Google Search and Maps',
            },
            {
              title: ' Google ?',
              content: '    Google Business Profile  Google Business Profile Help Center.',
            },
            {
              title: ' Google ?',
              content: ' Google   Google Maps & Search  ',
            },
            {
                title: ' Google?',
                content: ' Google  Reserve a Table   , ,    !  Google, ',
            },
            {
                title: ' Google ?',
                content: ' Reserve with Google  Google   Google Maps & Search    ',
            },
            {
                title: ' Google?',
                content: ' Google  Join Waitlist       !',
            },
            {
                title: ' Reserve with Google ?',
                content: ' Google  AAppoint  Reserve with Google',
            },
            {
                title: ' Google?',
                content: ' Google Business Profile Manager  >  ""  insights  Reserve with Google   AAppoint     www.aappoint.me',
            },
          ];

        return (
                    
        <section className="faq section-padding bg-grey pt-0" id="faq">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-lg-8">
                        <div className="section-heading center-heading text-center mb-60">
                            <h3 className="heading-title">?</h3>
                        </div>
                    </div>
                </div>

                <div className="row ">
      {accordionData.map((item, index) => (
        <div className="col-lg-6" key={index}>
          <Accordion className="panel-group">
            <Accordion.Item eventKey={index.toString()} className="panel-group">
              <Accordion.Header>
                <i className="more-less fa fa-plus"></i>{item.title}
              </Accordion.Header>
              <Accordion.Body className="panel-body">{item.content}</Accordion.Body>
            </Accordion.Item>
          </Accordion>
        </div>
      ))}
      
    
    </div>
            </div>
        </section>

        );
    }
}

export default Faq;
