import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap-icons/font/bootstrap-icons.css';


class Reserve extends Component {
    constructor(props){
        super(props);
        this.state = {
            about: [
                {
                    id: 1,
                    icon: 'bi bi-phone',
                    title: '',
                    text: ''
                },
                {
                    id: 2,
                    icon: 'bi bi-clipboard-data',
                    title: '',
                    text: '  , , -, '
                },
                {
                    id: 3,
                    icon: 'bi bi-envelope',
                    title: '',
                    text: ' SMS  E-mail '
                }
            ]
        }
    }
    render() {
        return (
        <section className="about-section section-padding bg-grey" id="reserve">
            <div className="container">
                <div className="row">
                <div className="col-lg-1 col-md-1">
                </div>

                    <div className="col-lg-4 col-md-4">
                        <div className="about-img">
                            <img src="assets/images/banner/reserve.png" alt="" className="img-fluid"/>
                        </div>
                    </div>
                    <div className="col-lg-1 col-md-1">
                </div>
                    <div className="col-lg-6 col-md-6">
                        <div className="section-heading">
                            {/* <span className="subheading">AAppoint X Reserve with Google</span> */}
                            <h3 className="heading-title">AAppoint X Reserve with Google.</h3>
                            <p>   Reserve a Table  Google  Google Maps ? </p>
                        </div>

                        {
                            this.state.about.map((data,i) => (
                                <div className="about-text-block" key={i}>
                                    <i className={data.icon}></i>
                                    <h4>{data.title}</h4>
                                    <p>{data.text} </p>
                                </div>
                            ))
                        }
                        
                        <Link to="#" className="btn btn-main-2"><i className="fa fa-check me-2"></i></Link> 
                    </div>
                </div>
            </div>
        </section>  
        );
    }
}

export default Reserve;
